body {
  margin: 0;
  padding: 0;
}

.col-s1 {width: 8.33%;}
.col-s2 {width: 16.66%;}
.col-s3 {width: 25%;}
.col-s4 {width: 33.33%;}
.col-s5 {width: 41.66%;}
.col-s6 {width: 50%;}
.col-s7 {width: 58.33%;}
.col-s8 {width: 66.66%;}
.col-s9 {width: 75%;}
.col-s10 {width: 83.33%;}
.col-s11 {width: 91.66%;}
.col-s12 {width: 100%;}

p {
  font-family: 'Oswald', sans-serif;
  font-size: 1.1em;
}

.container {
  position: relative;
  z-index: 2;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Yanone Kaffeesatz', sans-serif;
}

.justify-text {
  text-align: justify;
}

.App {
  height: 100vh;
  background-size: cover;
}

.app__background {
  position: absolute;
  height: inherit;
  background: url(/images/bk2.jpg);
  background-color: black;
  background-size: cover;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  z-index: 1;
}

.app__background-overlay {
  background: #1b1488;
  height: 100vh;
  position: absolute;
  opacity: 0.3;
}

.app__forms {
  position: relative;
  height: 100vh;
  z-index: 2;
}

.app__forms input {
  margin-bottom: 0 !important;
}

.app__signup {
  position: absolute;
  height: 70vh;
  background: white;
  left: 15%;
  top: 13%;
  padding: 2%;
  z-index: 3;
  -webkit-box-shadow: 0 3px 15px 4px rgba(0,0,0,0.4);
          box-shadow: 0 3px 15px 4px rgba(0,0,0,0.4);
}

.app__signup.animate-right {
  -webkit-animation: animate-right .5s forwards;
          animation: animate-right .5s forwards;
}

.app__signup.switch {
  left: 60%;
}

@-webkit-keyframes animate-right {
  0% {left: 15%;}
  100% {left: 60%;}
}

@keyframes animate-right {
  0% {left: 15%;}
  100% {left: 60%;}
}

.app__signup.return-left {
  -webkit-animation: animate-right .5s reverse;
          animation: animate-right .5s reverse;
}

.app__signup-container {
  margin-top: -7%;
  width: 91%;
  position: relative;
}

.app__signup-form-title {
  text-align: left;
  padding-left: 2%;
  color: #ddd;
}

.app__signup-border {
  margin: 10% 0;
  color: #1b1488;
}

.app__signup-button {
  font-family: 'Yanone Kaffeesatz', sans-serif;
  list-style-type: none;
  border: 1px solid #009bff;
  background-color: #009bff;
  padding-top: 1.5%;
  border-radius: 30px;
  width: 30%;
  height: 40px;
  margin: 0 auto;
  color: #fff;
  font-size: 17px;
}

.app__signup-change {
  display: none;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  list-style-type: none;
  border: 1px solid #009bff;
  background-color: #009bff;
  padding-top: 1.5%;
  border-radius: 30px;
  width: 30%;
  height: 40px;
  margin: 0 auto;
  color: #fff;
  font-size: 17px;
}

.app__submit-signup {
  font-family: 'Yanone Kaffeesatz', sans-serif;
  list-style-type: none;
  border: 1px solid #009bff;
  background-color: #009bff;
  padding-top: 0.5%;
  border-radius: 30px;
  width: 20%;
  height: 40px;
  margin: 1% auto;
  color: #fff;
  font-size: 17px;
}

.app__signup-button:hover {
  background: inherit;
  color: #1B1488;
  border-color: #1B1488;
}

.app__signin {
  position: absolute;
  height: 65vh;
  right: 20%;
  top: 15%;
  z-index: 2;
  padding: 7% 2%;
}

.app__signin.animate-left {
  -webkit-animation: animate-left .5s forwards;
          animation: animate-left .5s forwards;
}

.app__signin.switch {
  right: 35%;
  width: 60%;
}

@-webkit-keyframes animate-left {
  0% {right: 20%; width: 33.3%}
  100% {right: 35%; width: 60%;}
}

@keyframes animate-left {
  0% {right: 20%; width: 33.3%}
  100% {right: 35%; width: 60%;}
}

.app__signin.return-right {
  -webkit-animation: animate-left .5s reverse;
          animation: animate-left .5s reverse;
}

.app__signin .background {
  background-color: #1B1488;
  opacity: 0.7;
  height: inherit;
  position: inherit;
  top: 1%;
  left: -1%;
}

.app__signin-container {
  position: relative;
}

.app__signin-row {
  padding-left: 11%;
}

.app__signin-form-title {
  text-align: left;
  padding-left: 11%;
  color: #ddd;
}

.app__signin-form {
  padding: 2%;
}

.app__signin-form input:focus {
  border-bottom: 1px solid #fff !important;
  -webkit-box-shadow: 0 1px 0 0 #fff !important;
  box-shadow: 0 1px 0 0 #fff !important;
}

.app__signin-form input:focus + label {
  color: white !important;
}

.app__signin-row {
  color: #ddd;
}

.app__signin-button {
  font-family: 'Yanone Kaffeesatz', sans-serif;
  border: 1px solid #fff;
  background-color: inherit;
  padding-top: 1.5%;
  border-radius: 30px;
  width: 30%;
  height: 40px;
  margin: 0 auto;
  font-size: 18px;
  margin-left: 41%;
  color: white;
}

.app__signin-button:hover {
  color: #1B1488;
  background-color: #fff;
}

.important-msg {
  color: red;
  margin: 0;
  font-size: 12px;
}

.search-btn{
  margin-top: 15px;
}

.edit-profile-btn{
  margin-bottom: 10px;
}

.btn-spacing{
  margin-right: 5px !important;
}

.container-padding {
  padding-left: 5px !important;
  padding-right: 5px !important;
}

.image-container {
  width: 180;
  height: 220;
}

.center-container {
  margin: 0 auto !important;
}


.success-msg {
  color: green
}

.small-top-margin {
  margin-top: 5px;
}

